<template>
  <div class="main-wrapper">
    <div class="bread">
      <span class="bread-name">&nbsp;</span>
      <Breadcrumb separator=">">
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
    </div>
    <div class="content-wrapper">
      <div class="content-header">
        <Button
          class="btn-top-menu"
          custom-icon="sjzl-icon sjzl-icon-add"
          type="text"
          @click="handleOpt"
        >
          <span class="title"></span>
        </Button>
        <div class="search">
          <Input
            type="text"
            placeholder=""
            ref="searchinput"
            v-model="searchValue"
            @on-enter="search"
          />
          <Button
            type="default"
            shape="circle"
            class="search-circle"
            custom-icon="sjzl-icon sjzl-icon-search"
            @click="search"
          ></Button>
        </div>
      </div>
      <div class="h-line"></div>
      <div class="content-table">
        <Table
          ref="selection"
          stripe
          :columns="columns"
          :data="tabledata"
          tooltip-theme="light"
        >
          <template slot-scope="{ row, index }" slot="action">
            <Button type="text" class="btn-table" @click="editorSource(row, index)"></Button>
            <Button type="text" class="btn-table" @click="deleteSource(index)"></Button>
          </template>
        </Table>
      </div>
    </div>
    <Modal v-model="modalState">
      <p slot="header">
        <Icon custom=".sjzl-icon .sjzl-data-conpool"></Icon>
      </p>
      <Form
        :label-width="85"
        ref="formdata"
        :model="formdata"
        :rules="ruleValidate"
        label-colon
        inline
      >
        <FormItem label="" prop="databaseName">
          <Input
            ref="formValidateName"
            v-model="formdata.databaseName"
            placeholder=""
            elementId="name"
          ></Input>
        </FormItem>
        <FormItem label="" prop="databaseUrl">
          <Input ref="databaseUrl" v-model="formdata.databaseUrl" placeholder></Input>
        </FormItem>
        <FormItem label="" prop="username">
          <Input v-model="formdata.username" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="password">
          <Input v-model="formdata.password" placeholder="" type="password"></Input>
        </FormItem>
        <FormItem label="" prop="databaseType">
          <Select v-model="formdata.databaseType" style="width:200px">
            <Option
              v-for="item in statusList"
              :value="item.value"
              :key="item.value"
            >{{ item.label }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="handleSubmit('formdata')"></Button>
        <Button @click="handleReset('formdata')" style="margin-left: 8px"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import systemmanage from "./systemManage.class";
export default systemmanage;
</script>
<style lang="scss" scoped>
@import "./index.scss";
::v-deep .ivu-form-item-content {
  margin-left: 105px !important;
}
</style>