<template>
  <div class="page-warrper">
    <!-- <Button type="primary"  class="btn-l1 btn-confirm" shape="circle"></Button>
    <Button type="primary"  class="btn-l1 btn-cancel" shape="circle"></Button>-->
    <Button type="primary" :class="currentPage === 1?'active btn-l2':'btn-l2'" shape="circle" @click="handleChange(1)"></Button>
    <Button type="primary" :disabled="currentPage === 1" class="btn-l2" shape="circle" @click="handleChange(currentPage - 1)"></Button>
    <Page
      :class-name="compage"
      :total="totalCount"
      :current="currentPage"
      :page-size="pageSize"
      showt-elevator
      show-total
      @on-change="handleChange"
    >
      <Button
        type="primary"
        :disabled="currentPage ===  totalPage"
        class="btn-l2"
        shape="circle"
        @click="handleChange(currentPage + 1)"
      ></Button>
      <div class="ivu-page-options">
        <div class="ivu-page-options-elevator">
          
          <input type="text" autocomplete="off" spellcheck="false" value="" @keyup.enter="handleChange(Number($event.target.value))" ref="inputPage" @input="handleinputPage"/>
          /{{totalPage}}
          <slot>
            <span>{{totalPage}}</span>
          </slot>
        </div>
      </div>
    </Page>
  </div>
</template>
<script>
import comPage from  './page.class'
export default  comPage
</script>>
<style lang="scss" scoped>
@import "./index.scss";
</style>