import Vue from 'vue';
import Component from 'vue-class-component';
@Component({
    props: {
        totalCount: {
            type: Number,
            default: 0
        },
        totalPage: {
            type: Number,
            default: 0
        },
        current: {
            type: Number,
            default: 0
        },
        pageSize: {
            type: Number,
            default: 0
        }
    },
})
export default class comPage extends Vue {
    compage = "compage";
    get currentPage() {
        return this.current + 1
    }
    set currentPage(val) {
        this.$emit('update:current', val - 1)
    }
    handleChange(page) {
        page && (this.currentPage = page) && this.$emit('page-change', page);
        // console.log(page)
        this.$refs.inputPage.value = ""
    }
    handleinputPage() {
        // console.log(this.$refs.inputPage.value);
        if (/^[1-9]\d*$/.test(this.$refs.inputPage.value)) {
            console.log('value:', this.$refs.inputPage.value)
            this.$refs.inputPage.value = this.$refs.inputPage.value > this.totalPage ? '' : this.$refs.inputPage.value;
        } else {
            this.$refs.inputPage.value = this.$refs.inputPage.value.replace(/[^1-9]/g, '')
        }
    }
}
