import Vue from 'vue';
import Component from 'vue-class-component';
@Component({
    props:{
        btnArr:{
            type:Array,
            default:()=>[]
        },
        placeholder:{
            type:String,
            default:""
        },
        searchInput:{
            type:String,
            default:""
        },
        divider:{
            type:Boolean,
            default:true
        },
        header:{
            type:String,
            default:'content-header'
        }
    }
})
export default class topBtn extends Vue{
    btnMap = Object.freeze({
        'add':{
            title:'',
            icon:'add'
        },
        'addGroup':{
            title:'',
            icon:'add'
        },
        'refresh':{
            title:'',
            icon:'update'
        },
        'deleteBatch':{
            title:'',
            icon:'trash'
        },     
        'datamanage':{
            title:'',
            icon:'datamanage'
        },     
        'input':{
            title:'',
            icon:'import'
        },     
        'output':{
            title:'',
            icon:'output'
        },     
        'copy':{
            title:'',
            icon:'copy'
        }
    })
    get searchValue(){
        return this.searchInput
    }
    set searchValue(val){
        !this.searchValue && (this.searchValue.trim().length === 0)&& this.$emit('on-search',0,val);
    }
    
    handleOpt(type){
        this.$emit('on-opt',type)
    }

    search() {
        if (
          !this.searchValue ||
          this.searchValue.trim().length === 0
        ) {
          this.$refs["searchinput"].focus();
        } else {
            this.$emit('on-search',1);
        }
      };
}
