import Component, { mixins } from 'vue-class-component';
import tableMixins from '@/mixins/table.js'

@Component()

export default class systemmanage extends mixins(tableMixins) {
	//table
	modalState = false
	columns = [{
		type: "selection",
		width: 50,
		align: "center"
	}, {
		type: "index",
		width: 150,
		title: "",
		align: "left"
	}, {
		title: "",
		key: "databaseName",
	}, {
		title: "",
		key: "databaseType",
		ellipsis: true,
	}, {
		title: "",
		key: "databaseUrl",
		ellipsis: true,
		tooltip: true
	}, {
		title: "",
		key: "action",
		width: '150px',
		align: "left",
		slot: "action"
	}];
	tabledata = [];
	formdata = {
		databaseName: null,
		databaseUrl: null ,
		password: null,
		username: null,
		databaseType: 'oracle'
	}
	ruleValidate = {
		databaseName: [{ required: true, message: "", trigger: "blur", whitespace: true }],
		databaseUrl: [{ required: true, message: "", trigger: "blur" }],
		username: [{ required: true, message: "", trigger: "blur", whitespace: true }],
		password: [{ required: true, message: "", trigger: "blur"}],
		databaseType: [{ required: true,message: '', trigger: 'change'}]
	}
	statusList = [{
		label: 'Oracle',
		value: 'oracle'
	},  {
		label: 'Dm',
		value: 'dm'
	}]
	databaseList = []
	searchValue = ''

	titleMap = Object.freeze({
		add: { icon: "add", title: "", tip: "" },
	})

	btnArr = Object.freeze(['add', 'deleteBatch'])

	handleOpt() {
		this.formdata.databaseName = null;
		this.formdata.databaseUrl = null;
		this.formdata.databaseType = 'oracle';
		this.formdata.username = null;
		this.formdata.password = null;
		this.modalState = true
	};
	editorSource(row, index) {
		this.modalState = true
		console.log('indewx', row)
		this.formdata.databaseName = row.databaseName;
		this.formdata.databaseType = row.databaseType;
		this.formdata.databaseUrl = row.databaseUrl;
		this.formdata.password = row.password;
		this.formdata.username = row.username;
		this.formdata.id = row.id
	};
	deleteSource(index) {
		this.$api.DELETE_ID('/delete_source', {
			DBSourceId: this.tabledata[index].id
		}).then(res => {
			if (res.data.code === 0) {
				this.$Message.success('')
				this.tabledata.splice(index, 1)
			}
		})
	}
	search() { 
		console.log('sssss')
		this.$api.DbSourceNameLike('/db_source_name_like', {
			sourceName: this.searchValue
		}).then(res => {
			console.log('', res.data.body);
			this.tabledata = res.data.body
		})
	};
	handleSubmit(name) {
		console.log('nameeeeeeeeeeee', name)
		this.$refs[name].validate((valid) => {
			if (valid) {
				console.log('vvvvvvvvvvvvvvvvvvv', this.formdata)
				this.$api.ADD_DB_SOURCE('/add_db_source', this.formdata)
				.then(res => {
					console.log('rrrrrrrrrrr', res)
					if(res.data.code===0) {
						this.modalState = false
						this.tabledata = res.data.body
					}
				})
			} else {
				console.log('sssssssssssss')
				this.$Message.error('Fail!');
			}
		})
	}  
	created() {
		this.$api.GET_ALL('/list')
		.then(res => {
			this.tabledata = res.data.body
			console.log('tabledata', res.data.body)
		})
	}
}