<template>
  <div :class="header">
    <slot name="dropdown"></slot>
    <template v-for="(item,index) in btnArr">
      <Button
        type="text"
        class="btn-top-menu"
        :custom-icon="'sjzl-icon sjzl-icon-'+ btnMap[item].icon"
        @click="handleOpt(item)"
      >
        <span class="title">{{btnMap[item].title}}</span>
      </Button>
      <Divider type="vertical" v-if="divider && (index !== btnArr.length-1)"/>
    </template>
    <div class="search" v-if="placeholder">
      <Input
        type="text"
        :placeholder="placeholder"
        v-model="searchValue"
        ref="searchinput"
        @on-enter="search"
      />
      <Button
        type="default"
        shape="circle"
        class="search-circle"
        custom-icon="sjzl-icon sjzl-icon-search"
        @click="search"
      ></Button>
    </div>
  </div>
</template>
<script>
import "@style/index.scss";
import topBtn from "./top-btn.class";
export default topBtn;
</script>