import Vue from 'vue';
import Component from 'vue-class-component';
import page from "@common/com-page";
import topBtn from "@common/top-btn";

@Component({
  components: {
    page,
    topBtn
  },
})
export default class tableMixins extends Vue {
  tabledata = []
  rowId = null;
  sysId = null;
  sysName = null;
  loading = false;
  btnLoading = false;
  totalCount = 0;
  totalPage = 0;
  selectionList = new Set();
  formStatus = "";
  modalConfirm = false
  confirmStatus = "";
  modalTip = false;
  tipFlag = false;
  tipStatus = "";
  listQuery = {
    pageIndex: 0,
    size: 14,
    searchValue: '',
    order: null,
    sort: null,
  };
  timer = null;
  confirmMap = Object.freeze({
    confirm: {
      icon: "confirm",
      text: "",
      content: "",
      type: ""
    },
    message: {
      icon: "message",
      text: "",
      content: "!",
      type: ""
    },
  })
  tipMap = Object.freeze({
    update: {
      icon: "update",
      content: " !"
    },
    success: {
      icon: "confirm",
      content: ""
    },
    error: {
      icon: "alert",
      content: ""
    }
  });
  handleSpin(title) {
    this.$Spin.show({
        render: (h) => {
            return h('div', {
              style:{
                // backg
              }
            },[
                h('Icon', {
                    class: 'spin-icon-load',
                    props: {
                        type: 'ios-loading',
                        size: 20
                    }
                }),
                h('div', {
                  style:{
                    color:'#3972f2',
                    fontSize:'16px',
                    fontWeight:'550',
                    marginTop:'10px'
                  }
                },title || '')
            ])
        }
    });
  }
  onSelect(selection, row) {
    row.id ? (row.id = row.id) : (row.id = row[this.rowKey])
    this.selectionList.add(row.id);
    this.rowMap && (this.rowMap[row.id] = row[this.rowType]);
  };
  onSelectCancel(selection, row) {
    row.id ? (row.id = row.id) : (row.id = row[this.rowKey])
    this.selectionList.delete(row.id);
    this.rowMap && delete this.rowMap[row.id];
  };
  onSelectAll(selection) {
    for (let item of selection) {
      if (!this.selectionList.has(item)) {
        this.selectionList.add(item.id ? item.id : item[this.rowKey]);
        this.rowMap && (this.rowMap[item.id ? item.id : item[this.rowKey]] = item[this.rowType]);
      }
    }
  };
  onSelectAllCancel(selection) {
    // console.log("on-select-all-cancel",JSON.stringify(String(Array.from(this.selectionList))));
    this.selectionList.clear();
    this.rowMap && (this.rowMap = {});
  };
  onFormVisibleChange(visible) {
    console.log('visible', visible)
  }

  getsysInfo() {
    return new Promise((resolve, reject) => {
      // this.apiMap.get({ id: this.sysId }).then(res => {
      //   if (res.data.code === 0) {
      //     if (res.data.body) {
      //       this.sysName = res.data.body.systemName
      //       resolve('success')
      //     } else {
      //       this.handleTipModal("error", "", 1000);
      //       setTimeout(() => {
      //         this.$router.replace({ path: '/datasource' })
      //       }, 2000);
      //     }
      //   } else {
      //     resolve('error')
      //     this.handleConfirmModal('message','',res.data.msg || "");
      //   }
      // })
    })
  }

  getTableList() {
    // this.loading = true;
    // this.handleSpin();
    return new Promise((resolve, reject) => {
      let params;
      if(this.getTableKey){
        let obj = {}
        obj[this.getTableKey] = this[this.getTableValue]
        params = Object.assign(this.listQuery,obj)
      } else{
        params = this.sysId ? Object.assign(this.listQuery, { sysId: this.sysId }):this.listQuery
      } 
      // this.apiMap.find(params).then(res => {
      //   // this.$Spin.hide();
      //   if (res.data.code === 0) {
      //     const data = res.data.body;
      //     this.tabledata = _.cloneDeep(data.list);
      //     // debugger
      //     this.totalCount = data.totalCount;
      //     this.totalPage = data.totalPage;
      //     resolve('success');
      //   } else {
      //     resolve('error')
      //     // this.handleTipModal("error", res.data.msg || "", 1000);
      //     this.handleConfirmModal('message','',res.data.msg || "");
      //   }
      //   this.loading = false;
      // });
    })
  };
  
  resetForm(){
    this.$refs["formValidate"].resetFields();
  }

  handleTipModal(tipstatus, content, timeout=1000) {
    this.tipStatus = tipstatus;
    if (content.trim().length) {
      this.tipMap[this.tipStatus].content = content;
    }
    this.modalTip = true;
    this.timer = setTimeout(() => {
      this.modalTip = false
    }, timeout);
  };

  handleConfirmModal(confirmStatus='message',type,content='') {
    this.confirmStatus = confirmStatus;
    type && (this.confirmMap[confirmStatus].type = type);
    if (content.trim().length) {
      this.confirmMap[confirmStatus].content = content;
    }
    this.modalConfirm = !this.modalConfirm;
  };

  onTipVisibleChange(visible){
    if(!visible){
      this.tipStatus = '';
      clearTimeout(this.timer);
      this.timer = null;
    }
  }
  //refresh
  handleRefresh() {
    // this.listQuery.pageIndex = 0;
    this.listQuery.searchValue = null;
    this.getTableList().then(res => {
      res === 'success' && this.handleTipModal("update", "");
    })
  };
  //
  search(type,val='') {
    console.log(type)
    if (!type) {
      this.listQuery.searchValue = val;
    } else {
      this.listQuery.pageIndex = 0;
      this.getTableList().then(res => {
        this.listQuery.searchValue = '';
      });
    }
  };
  handlePost(params, opt,config = {}) {
    // console.log(params)
    let that = this;
    return new Promise(function (resolve, reject) {
      // that.apiMap[opt](params,config).then(res => {
      //   that.btnLoading = false;
      //   if (res.data.code === 0) {
      //     that.modalConfirm = false;
      //     if (opt !== 'test') {
      //       that.modalForm = false;
      //       that.listQuery.pageIndex = 0;
      //       that.getTableList().then(res => {
      //         that.handleTipModal("success", "", 1000);
      //       })
      //     }
      //     resolve("success");
      //   } else {
      //     console.log('error')
      //     // that.handleTipModal("error", res.data.msg || "", 1000);
      //     that.handleConfirmModal('message','',res.data.msg || "");
      //   }
      // });
    });
  };

  handlePostUrl(url, params, opt,config = {}) {
    let that = this;
    return new Promise(function (resolve, reject) {
      that.apiMap[opt](url, params,config).then(res => {
        that.btnLoading = false;
        if (res.data.code === 0) {
          that.modalConfirm = false;
          that.listQuery.pageIndex = 0;
          that.getTableList().then(res => {
            that.handleTipModal("success", "", 1000);
          })
          resolve("success");
        } else {
          console.log('error')
          that.handleConfirmModal('message','',res.data.msg || "");
        }
      });
    });
  };
}